.tesla-car {
    width: 100%;
    min-height: 350px;
    background: #fff url(../../assets/tesla.jpg) no-repeat top center;
    background-size: contain;
}

.tesla-wheels {
    height: 247px;
    width: 555px;
    position: relative;
    margin: 0 auto;
}

.tesla-wheel {
    height: 80px;
    width: 80px;
    bottom: 0;
    position: absolute;
    background-repeat: no-repeat;
    background-position: 0 0;
    background-size: cover; }

.tesla-wheel--front {
    left: 53px; }

.tesla-wheel--rear {
    right: 72px; }

.tesla-wheel--19 {
    background-image: url(../../assets/wheel-19.png);
    -webkit-animation: infinite-spinning 250ms steps(6) infinite;
    animation: infinite-spinning 250ms steps(6) infinite; }

.tesla-wheel--21 {
    background-image: url(../../assets/wheel-21.png);
    -webkit-animation: infinite-spinning 600ms steps(12) infinite;
    animation: infinite-spinning 600ms steps(12) infinite; }

@keyframes infinite-spinning {
    from {
      -webkit-transform: rotate(0deg);
      -ms-transform: rotate(0deg);
      transform: rotate(0deg); }
    to {
      -webkit-transform: rotate(360deg);
      -ms-transform: rotate(360deg);
      transform: rotate(360deg); } }
  @-webkit-keyframes infinite-spinning {
    from {
      -webkit-transform: rotate(0deg);
      -ms-transform: rotate(0deg);
      transform: rotate(0deg); }
    to {
      -webkit-transform: rotate(360deg);
      -ms-transform: rotate(360deg);
      transform: rotate(360deg); }
}


